<template>
	<div>
		<el-row>
  			<el-col :span="7" id='order'>
  				<!--我是订单-->
  				<el-tabs type="card" style='height:100%' >
    				<el-tab-pane label="点餐">
    					<el-table :data="tableData" style="width: 100%">
      						<el-table-column prop="goodsName" label="商品名称"> </el-table-column>
      						<el-table-column prop="count" label="数量" width="70"> </el-table-column>
      						<el-table-column prop="price" label="金额" width="70"> </el-table-column>
      						<el-table-column label="操作" width="100" fixed='right'>
      							<template scope='scope'>
      						   		<el-button type="text" size="small" @click='del(scope.row)'>删除</el-button>
      						   		<el-button type="text" size="small" @click='addOrder(scope.row)'>增加</el-button>
      							</template>
      						</el-table-column>      						
    					</el-table>				
    					<div class='totaldiv'>
    						<span>数量：{{totalcount}} </span>
    						<span>金额：{{totalmoney}}元</span>
    					</div>
    					<br />
    					<el-button type="warning" >挂单</el-button>
						<el-button type="danger" @click='delAll'>删除</el-button>
						<el-button type="success" @click='checkOut'>结账</el-button> 	
    				</el-tab-pane>
    				<el-tab-pane label="挂单" >
    					<el-table :data="tableData2" style="width: 100%">
      						<el-table-column prop="goodId" label="编号"> </el-table-column>
      						<el-table-column prop="price" label="金额" width="70"> </el-table-column>
      						<el-table-column prop="menu" label="挂单详情" width="150"> </el-table-column>
      						<el-table-column label="操作" width="70" fixed='right'>
      							<template scope='scope'>
      						 		<el-button type="text" size="small">付款</el-button>
      							</template>
      						</el-table-column>
    					</el-table>
    				</el-tab-pane>
    				<el-tab-pane label="外卖">外卖</el-tab-pane>
  				</el-tabs>			
  			</el-col>
  			<el-col :span="17">
  				<div class="often-goods">
  					<div class="title">常用商品</div>
  					<div class="often-goods-list">
  						<ul>
  							<li v-for='goods in oftenGoods' @click='addOrder(goods)'>
  								<span>{{goods.goodsName}}</span>
  								<span class='price'>{{goods.price}}元</span>
  							</li>
  						</ul>
  					</div>
  				</div>
  				<div class="goods-type">
  				    <el-tabs>
  				    	<el-tab-pane label='汉堡'>
  				    		<ul class='list'>
  				    			<li v-for='goods in type0Goods' @click='addOrder(goods)'>
  				    				<img :src="goods.goodsImg" alt="" />
  				    				<p>{{goods.goodsName}}</p>
  				    				<p>￥{{goods.price}}元</p>
  				    			</li>
  				    		</ul>
  				    	</el-tab-pane>
  				    	<el-tab-pane label='小食'>
  				    		<ul class='list'>
  				    			<li v-for='goods in type1Goods' @click='addOrder(goods)'>
  				    				<img :src="goods.goodsImg" alt="" />
  				    				<p>{{goods.goodsName}}</p>
  				    				<p>￥{{goods.price}}元</p>
  				    			</li>
  				    		</ul>
  				    	</el-tab-pane>
  				    	<el-tab-pane label='饮料'>
  				    		<ul class='list'>
  				    			<li v-for='goods in type2Goods' @click='addOrder(goods)'>
  				    				<img :src="goods.goodsImg" alt="" />
  				    				<p>{{goods.goodsName}}</p>
  				    				<p>￥{{goods.price}}元</p>
  				    			</li>
  				    		</ul>
  				    	</el-tab-pane>
  				    	<el-tab-pane label='套餐'>
  				    		<ul class='list'>
  				    			<li v-for='goods in type3Goods' @click='addOrder(goods)'>
  				    				<img :src="goods.goodsImg" alt="" />
  				    				<p>{{goods.goodsName}}</p>
  				    				<p>￥{{goods.price}}元</p>
  				    			</li>
  				    		</ul>
  				    	</el-tab-pane>
  				    </el-tabs>
  				</div>
  			</el-col>
		</el-row>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		name:'pos',
		data(){
			return {
				tableData: [],
				tableData2: [],
				oftenGoods:[],
			    type0Goods:[],
			    type1Goods:[],
			    type2Goods:[],
			    type3Goods:[],
			    totalcount:0,
			    totalmoney:0
			}
		},
		created(){
			axios.get('http://jspang.com/DemoApi/oftenGoods.php')
			.then(response=>{
				this.oftenGoods=response.data;
			}).catch(error=>{
				alert('网络出错，无法正常访问')
			});
			axios.get('http://jspang.com/DemoApi/typeGoods.php')
			.then(response=>{
				this.type0Goods=response.data[0];
				this.type1Goods=response.data[1];
				this.type2Goods=response.data[2];
				this.type3Goods=response.data[3];
			})
			.catch(error=>{
				console.log(error);
				alert('网络出错，无法正常访问')
			})
		},
		mounted:function (){
			var orderHeight=document.body.clientHeight;
			document.getElementById('order').style.height=orderHeight+'px';
		},
		methods:{
			//添加到到订单列表
			addOrder(goods){
				let isHave=false;   //不存在
				//判断某个商品是否存在于订单栏中
				for(let i=0;i<this.tableData.length;i++){
					if(this.tableData[i].goodsId==goods.goodsId){
						isHave=true;   //存在
					}
				}
				//根据isHave的值判断列表中是否已存在该商品
				if(isHave){
					//改变列表中某个商品的数量
					//使用filter对tableData进行过滤，将符合条件的存入arr中
					let arr=this.tableData.filter(obj=>obj.goodsId==goods.goodsId);
					arr[0].count++;
				}else{
					let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1}
				    this.tableData.push(newGoods);
				}
				this.getAllMoney();
			},
			//数量和价格汇总
			getAllMoney(){
				this.totalcount=0;
				this.totalmoney=0;
				if(this.tableData){
					this.tableData.forEach(element=>{
						this.totalcount+=element.count;
						this.totalmoney=this.totalmoney+(element.count*element.price)
					})
				}				
			},
			del(goods){
				this.tableData=this.tableData.filter(obj => obj.goodsId!=goods.goodsId);
				this.getAllMoney();
			},
			delAll(){
				this.tableData=[];
				this.totalcount=0;
				this.totalmoney=0;
			},
			checkOut(){
				if(this.totalcount!=0){
					this.tableData=[];
					this.totalcount=0;
					this.totalmoney=0;
					this.$message({
						message:'结账成功',
						type:'success'
					});
				}else{
					this.$message.eror('结账失败，请重新操作');
				}
			}
		}
	}
</script>

<style>
	#order{
		border-right:1px solid #ccc;
		background-color: #fff;
	}
	.cell{
		text-align: center;
		font-size:17px;
	}
	.totaldiv{
		border-bottom: 1px solid #ccc;
		padding:10px 0px;
	}
	.totaldiv span:first-child{
		padding-right:50px;
	}
	.often-goods{
		background-color: #f9fafc;
	}
    .title{
    	text-align: left;
    	border-bottom:1px solid #d3dce6;
    	
    	padding:10px;
    }
    .often-goods-list{
    	padding:30px;
    	overflow:hidden;
    }
    .often-goods-list ul li{
    	float: left;
    	padding:10px;
    	margin:10px;
    	cursor: pointer;
    	list-style: none;
    	border-radius: 5px;
    	border:1px solid #E5E9F2;
    	background-color: #fff;
    }
    .price{
    	color:#58B7FF;
    }
    .goods-type{
    	clear:both;
    }
    .goods-type .list{
    	margin:10px;
    }
    .goods-type li{
    	list-style: none;
    	width: 20%;
    	border:1px solid #E5E9F2;
    	height:auto;
    	overflow: hidden;
    	background-color: #fff;
    	padding:2px;
    	float: left;
    	margin:10px;
    	cursor: pointer;
    }
    .goods-type li span{
    	display:inline-block;
    	/*float:left;*/
    }
    .goods-type li img{
    	width:35%;
    	float:left;
    }
    .goods-type li p:nth-of-type(1){
    	font-size:18px;
    	padding-top:8px;
    	
    }
    .goods-type li p:nth-of-type(2){
    	font-size:16px;
    	padding:10px 0px 8px 10px; 
    	color:brown; 	
    }
    .goods-type .el-tabs__nav {
    	margin-left:5px;
    }
    .el-tabs__item{
    	font-size:18px;
    }
</style>


